.bcolor{
    background-color: #7f5a83;
    background-image: linear-gradient(315deg, #7f5a83 0%, #0d324d 74%);

}

h1.title{
    text-align: center;
    font-weight: 600;
    font-size: 50px;
    color: white;
}

p.subtitle{
    font-size: 30px;
    text-align: center;
    color: white;
}

@media only screen and (max-width: 768px) {
    h1.title{
        font-size:25px;
    }

    p.subtitle{
        font-size:15px;
    }

}